.BreadBottom {
    height: 13%;
    width:80%;
    background: linear-gradient(#F08E4A, #e27b36);
    border-radius: 0 0 30px 30px;
    box-shadow: inset -15px 0 #c15711;
    margin: 2% auto;
}

.BreadTop {
    height: 20%;
    width: 80%;
    background: linear-gradient(#bc581e, #e27b36);
    border-radius: 50% 50% 0 0;
    box-shadow: inset -15px 0  #c15711;
    margin: 2% auto;
    position: relative;
}

.Seeds1 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: #fff;
    position: absolute;
    left: 30%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(-20deg);
    box-shadow: inset -2px -3px  #c9c9c9;
}

.Seeds1:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #fff;
    left: -170%;
    top: -260%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px 2px  #c9c9c9;
}

.Seeds1::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #fff;
    left: -170%;
    top: -260%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px -3px  #c9c9c9;
}

.Seeds2 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: #fff;
    left: 64%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(10deg);
    box-shadow: inset -3px 0  #c9c9c9;
}

.Seeds2::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #fff;
    left: 150%;
    top: -130%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset 1px 3px  #c9c9c9;
}

.Seeds2::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #fff;
    left: -150%;
    top: -130%;
    border-radius: 40%;
    transform: rotate(90deg);
    box-shadow: inset 1px 3px  #c9c9c9;
}

.Meat {
    width: 80%;
    height: 8%;
    background: linear-gradient(#7f3608, #702e05);
    margin: 1% auto;
    border-radius: 15px;
}

.Cheese {
    width: 90%;
    height: 4.5%;
    margin: 1% auto;
    background: linear-gradient(#f5d004, #d6bb22);
    border-radius: 20px;
}

.Salad {
    
    width: 85%;
    height: 7%;
    background: linear-gradient(#228c1d, #91ce50);
    margin: 1% auto;
    border-radius: 20px;
}

.Bacon {
    width: 80%;
    height: 4%;
    background: rgb(226, 67, 67);
    margin: 1% auto;
    border-radius: 5px;
}