.BuildControls {
    width: 100%;
    background: #cf8f2e;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 2px 1px #eee;
    margin: auto;
    padding: 10px 0;

    .OrderButton {
       background-color: #DAD735;
       outline: none;
       cursor: pointer;
       border: 1px solid #966909;
       color: #966909;
       font-family: inherit;
       font-size: 1.2em;
       padding: 15px 30px;
       box-shadow: 2px 2px  #966909; 
    }

    .OrderButton:hover,.OrderButton:active {
        background-color: #a0db41;
        border: 1px solid #966909;
        color: #966909;
    }

    .OrderButton:disabled {
        background-color: #c7c6c6;
        cursor: not-allowed;
        border: 1px solid #ccc;
        color: #888888
    }

    .OrderButton:not(:disabled) {
        animation: enable .3s linear;
    }

    @keyframes enable {
        0%{transform: scale(1)}
        60%{transform: scale(1.1)}
        100%{transform: scale(1)}
    }
}