.SideDrawer {
    position: fixed;
    width: 280px;
    min-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    background-color: #fff;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform .3s ease-out;   
}

@media (min-width: 600px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    transform: translateX(0);
}

.Close {
    transform: translateX(-100%);
}